body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Message {
    line-height: 1.5em;
    font-size: 1.5em;
  }

  .Message.log {
    color: #bbb;
  }

  .Message .author {
    font-weight: bold;
    color: #888;
    margin: 0 5px 0 0;
  }

  .Message.me .author {
    color:#000080;
  }

.MessageList {
    overflow-y: auto;
    padding: 4px;
  }

.MessageForm {
    display: -webkit-flex;
    display: flex;
    background: #ccc;
  }

  .MessageForm .input-container {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 1px;
  }

  .MessageForm .button-container {
    -webkit-flex: 0 0 6em;
            flex: 0 0 6em;
    margin: 1px 1px 1px 0;
  }

  .MessageForm input {
    background: #fff;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    height: 100%;
    outline: none;
    width: 100%;
  }

  .MessageForm button {
    background: #0066cc;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    color:white;
    height: 100%;
    outline: none;
    width: 100%;
  }

  .MessageForm input {
    padding: 0 0 0 8px;
  }

.App {
  border: 1.5px solid #ccc;
  border-radius: 3px;
  height: 98vh;
  width: 98%;
  margin-top: 0.7vh;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto;
  font-size: 13x;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

.App .MessageList {
  box-sizing: border-box;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App .MessageForm {
  background: #eee;
  border-top: 1px solid #ccc;
  border-radius: 0 0 3px 3px;
  box-sizing: border-box;
  -webkit-flex: 0 0 30px;
          flex: 0 0 30px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App h1 b {
    position: relative;
    bottom: 1.4rem;
}

.App h1 img {
    position: relative;
    margin-left: 1em;
    bottom: 0.8rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

